/**
* 法律法规组件
* @module 案件
* @desc 历史版本
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <el-dialog
                :close-on-click-modal="false"
                :title="TableObj.title"
                :visible.sync="TableObj.show"
                append-to-body
                center
                top="50px"
                width="70%"
        >
            <el-table
                    :data="list"
                    :header-cell-style="{background:'#e9eff8'}"
                    @row-click="seeFunction"
                    border
                    size="mini"
                    v-loading="loading"
            >
                <el-table-column align="center" label="案由名称" prop="reasonName"/>
                <el-table-column
                        :show-overflow-tooltip="true"
                        align="center"
                        label="违反条例"
                        prop="violationRegulations"
                />
                <el-table-column
                        :show-overflow-tooltip="true"
                        align="center"
                        label="违反条例内容"
                        prop="violationRegulationsContent"
                />
                <el-table-column
                        :show-overflow-tooltip="true"
                        align="center"
                        label="依据条例"
                        prop="accordingRegulations"
                />
                <el-table-column
                        :show-overflow-tooltip="true"
                        align="center"
                        label="职权依据"
                        prop="rightclause"
                />
                <el-table-column align="center" label="是否生效" prop="isEffective">
                    <template slot-scope="scope">{{scope.row.isEffective|isEffective}}</template>
                </el-table-column>
                <el-table-column align="center" label="操作" width="150">
                    <template slot-scope="scope">
                        <el-button
                                @click="editFunction(scope.row)"
                                plain
                                size="mini"
                                type="warning"
                                v-if="scope.row.isEffective==2"
                        >编辑
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!-- 编辑&添加弹框 -->
            <change-Edit
                    :okbtn="okbtn"
                    :see="see"
                    :show.sync="editObj.show"
                    :title="editObj.title"
                    :type="editObj.editId"
            />
            <!--        分页-->
            <iPagination
                    :pageNum.sync="form.pageNum"
                    :pageSize.sync="form.pageSize"
                    :total="total"
                    @getData="getAccountList"
            ></iPagination>
        </el-dialog>
        <!-- 查看 -->
        <see-content :editId="seeObj.editId" :show.sync="seeObj.show" :title="seeObj.title"></see-content>
    </div>
</template>
<script>
    import {apiCaseReasonSearch, apicCaseReasonDelete} from "../../../../api/case";
    import {isEffective} from "../../../../until/wordBook";
    import iPagination from "../../../../components/LjPagination";
    import changeEdit from "./editadd";
    import seeContent from "./seeContent";

    export default {
        name: "historyTable",
        props: {
            TableObj: {
                type: Object,
                default: null
            }
        },
        data() {
            return {
                loading:true,
                form: {
                    pageSize: 1000,
                    pageNum: 1
                },
                total: 100,
                list: [],
                see: false,
                editObj: {
                    title: "",
                    editId: null,
                    show: false
                },
                seeObj: {
                    show: false,
                    title: "",
                    editId: null
                },
                okbtn: ""
            };
        },
        components: {
            iPagination,
            changeEdit,
            seeContent
        },
        methods: {
            //查看
            seeFunction(row) {
                this.seeObj.show = true;
                this.seeObj.title = "查看全部";
                this.seeObj.editId = row.crId;
            },

            //编辑
            editFunction(row) {
                this.editObj.editId = row.crId; //根据是否传editId判断是否是编辑和添加
                this.editObj.title = "修改";
                this.editObj.show = true;
                this.okbtn = "1";
                this.see = false;
            },

            //删除
            delateFunction(row) {
                this.$confirm("此操作将永久删除, 是否继续?", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning"
                }).then(() => {
                    apicCaseReasonDelete(row.crId).then(res => {
                        this.getAccountList();
                        this.$message({
                            type: "success",
                            message: "删除成功!"
                        });
                    });
                });
            },

            //弹框弹出时获取data给form
            getAccountList() {
                this.form.reasonId = this.TableObj.editId;
                apiCaseReasonSearch(this.form).then(res => {
                    this.loading=false;
                    this.total = res.total;
                    this.list = res.data;
                    this.$emit("getList");
                });
            }
        },
        filters: {
            //是否
            isEffective(value) {
                return isEffective[value];
            },

        },
        //监听，如果show为true,则执行initPage()
        watch: {
            "TableObj.show"(value) {
                if (value) {
                    this.getAccountList();
                }
            }
        }
    };
</script>
<style lang='less' scoped>
</style>


