/**
* 法律法规组件
* @module 案件
* @desc 添加&修改
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <el-dialog
                :close-on-click-modal="false"
                :title="title"
                :visible="show"
                @close="closeDialog"
                append-to-body
                center
                top="30px"
                width="800px"
        >
            <el-form :model="form" :rules="rules" label-width="150px" ref="ruleForm" size="medium" v-loading="loading">
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="执法领域：">
                            <span class="fontcss">{{form.enforceName}}</span>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="法律法规：">
                            <span class="fontcss">{{form.lawName}}</span>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="处罚事项：">
                            <span class="fontcss">{{form.pmName}}</span>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="案由编号：" v-if="type">
                            <span class="fontcss">{{form.reasonNum}}</span>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                      <el-form-item label="案由名称：" prop="reasonName">
                        <el-input placeholder="请输入案由名称" v-model="form.reasonName"></el-input>
                      </el-form-item>
                    </el-col>

                    <el-col :span="24">
                        <el-form-item label="违反条例：" prop="violationRegulations">
                            <el-input placeholder="请输入违反条例" v-model="form.violationRegulations"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="违反条例内容：" prop="violationRegulationsContent">
                            <el-input
                                    :autosize="{ minRows: 2, maxRows: 4}"
                                    placeholder="请输入违反条例内容"
                                    type="textarea"
                                    v-model="form.violationRegulationsContent"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="依据条例：" prop="accordingRegulations">
                            <el-input placeholder="请输入依据条例" v-model="form.accordingRegulations"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="依据条例内容：" prop="accordingRegulationsContent">
                            <el-input
                                    :autosize="{ minRows: 2, maxRows: 4}"
                                    placeholder="请输入依据条例内容"
                                    type="textarea"
                                    v-model="form.accordingRegulationsContent"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="职权依据：" prop="rightclause">
                            <el-input placeholder="请输入职权依据" v-model="form.rightclause"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="自然人罚款下限：" prop="naturalRangeMin">
                            <el-input-number
                                    :min="0"
                                    :step="50"
                                    placeholder="自然人罚款下限"
                                    v-model="form.naturalRangeMin"
                            ></el-input-number>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="自然人罚款上限：" prop="naturalRangeMax">
                            <el-input-number
                                    :min="form.naturalRangeMin"
                                    :step="50"
                                    placeholder="自然人罚款上限"
                                    v-model="form.naturalRangeMax"
                            ></el-input-number>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="法人罚款下限：" prop="legalRangeMin">
                            <el-input-number
                                    :min="0"
                                    :step="50"
                                    placeholder="法人罚款下限"
                                    v-model="form.legalRangeMin"
                            ></el-input-number>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="法人罚款上限：" prop="legalRangeMax">
                            <el-input-number
                                    :min="form.legalRangeMin"
                                    :step="50"
                                    placeholder="法人罚款上限"
                                    v-model="form.legalRangeMax"
                            ></el-input-number>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="是否生效：" prop="isEffective" v-if="okbtn!=2">
                            <el-switch :active-value="1" :inactive-value="2" v-model="form.isEffective"></el-switch>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="生效日期：" prop="effectiveTime" v-if="form.isEffective==2">
                            <el-date-picker placeholder="选择生效日期" type="date"
                                            v-model="form.effectiveTime"></el-date-picker>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <span class="dialog-footer" slot="footer">
        <el-button @click="closeDialog" size="small" type="info">
          <i class="iconfont icon-back"></i>取 消
        </el-button>
        <el-button @click="submitForm('ruleForm')" size="small" type="primary">
          <i class="iconfont icon-save"></i>确 定
        </el-button>
      </span>
        </el-dialog>
    </div>
</template>
<script>
    import {
        apiCaseReasonAdd,
        apiCaseReasonGet,
        apiCaseReasonRevise,
        apiPunishMatterGet,
        paiCaseReasonUpdate
    } from "../../../../api/case";
    import {formatDate} from "../../../../until/wordBook";

    export default {
        name: "edit",
        props: {
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: "编辑"
            },
            pmId: {
                type: String,
                default: ""
            },
            tid:{
                type: String,
                default: ""
            },
            type: {
                type: String,
                default: ""
            },
            okbtn: {
                type: String,
                default: ""
            },
            see: {
                type: Boolean,
                default: false
            }
        },
        data() {
            return {
                loading:true,
                form: {
                    pageSize: 20,
                    pageNum: 1
                },
                rules: {
                    enforceId: [
                        {required: true, message: "请选择内容", trigger: "change"}
                    ],
                    lawId: [{required: true, message: "请选择内容", trigger: "change"}],
                    pmId: [{required: true, message: "请选择内容", trigger: "change"}],
                    reasonName: [
                        {required: true, message: "请输入内容", trigger: "blur"}
                    ],
                    reasonNum: [{required: true, message: "请输入内容", trigger: "blur"}],
                    violationRegulations: [
                        {required: true, message: "请输入内容", trigger: "blur"}
                    ],
                    violationRegulationsContent: [
                        {required: true, message: "请输入内容", trigger: "blur"}
                    ],
                    accordingRegulations: [
                        {required: true, message: "请输入内容", trigger: "blur"}
                    ],
                    accordingRegulationsContent: [
                        {required: true, message: "请输入内容", trigger: "blur"}
                    ],
                    rightclause: [
                        {required: true, message: "请输入内容", trigger: "blur"}
                    ],
                    naturalRangeMin: [
                        {required: true, message: "请选择内容", trigger: "blur"}
                    ],
                    legalRangeMin: [
                        {required: true, message: "请选择内容", trigger: "blur"}
                    ],
                    naturalRangeMax: [
                        {required: true, message: "请选择内容", trigger: "blur"}
                    ],
                    legalRangeMax: [
                        {required: true, message: "请选择内容", trigger: "blur"}
                    ],

                    effectiveTime: [
                        {required: true, message: "请选择内容", trigger: "blur"}
                    ],
                    isEffective: [
                        {required: true, message: "请选择内容", trigger: "blur"}
                    ]
                }
            };
        },
        methods: {
            closeDialog() {
                this.$emit("update:show", false);
            },

            //提交
            submitForm() {
                this.$refs.ruleForm.validate(valid => {
                    if (valid) {
                        if (this.form.effectiveTime) {
                            this.form.effectiveTime = Date.parse(this.form.effectiveTime);
                        }

                        if (this.okbtn == 1) {
                            //编辑
                            this.loading=true;
                            paiCaseReasonUpdate(this.form).then(res => {
                                this.loading=false;
                                this.closeDialog();
                                this.$emit("getList");
                                this.$message({
                                    type: "success",
                                    message: "修改成功!"
                                });
                            });
                        } else if (this.okbtn == 2) {
                            this.loading=true;
                            //添加
                            apiCaseReasonAdd(this.form).then(res => {
                                this.loading=false;
                                this.$emit("getList");
                                this.closeDialog();
                                this.$message({
                                    type: "success",
                                    message: "添加成功!"
                                });
                            });
                        } else if (this.okbtn == 3) {
                            this.loading=true;
                            //修订案由
                            apiCaseReasonRevise(this.form).then(res => {
                                this.loading=false;
                                this.$emit("getList");
                                this.closeDialog();
                                this.$message({
                                    type: "success",
                                    message: "修订成功!"
                                });
                            });
                        }
                    }
                });
            },

            //弹框弹出时获取data给form
            initPage() {
                if (this.type) {
                    //编辑
                    apiCaseReasonGet(this.type).then(res => {
                        this.loading=false;
                        this.form = res.data;
                    });
                } else {
                    //添加
                    apiPunishMatterGet(this.tid).then(res => {
                        this.loading=false;
                        this.form = {
                            enforceName: res.data.enforceName,
                            lawName: res.data.lawName,
                            pmName: res.data.pmName,
                            enforceId: res.data.enforceId,
                            lawId: res.data.lawId,
                            pmId: res.data.pmId,
                            lawVersion: res.data.lawVersion,
                            pmVersion: res.data.version,
                            violationRegulations: res.data.violationRegulations,
                            violationRegulationsContent: res.data.violationRegulationsContent,
                            accordingRegulations: res.data.accordingRegulations,
                            accordingRegulationsContent: res.data.accordingRegulationsContent,
                            rightclause: res.data.rightclause,
                            naturalRangeMin: res.data.naturalRangeMin,
                            naturalRangeMax: res.data.naturalRangeMax,
                            legalRangeMin: res.data.legalRangeMin,
                            legalRangeMax: res.data.legalRangeMax,
                            isEffective: 1,
                            isDisable: 1,
                            enforceOrder: 0
                        };
                    });
                }
            }
        },

        filters: {
            //时间戳转换
            formatDate(value) {
                return formatDate(value, "yyyy-MM-dd hh:mm:ss");
            }
        },
        //监听，如果show为true,则执行initPage()
        watch: {
            show(value) {
                if (value) {
                    this.initPage();
                    //清除表单验证
                    this.$nextTick(() => {
                        this.$refs.ruleForm.clearValidate();
                    });
                }
            }
        }
    };
</script>
<style lang='less' scoped>
    .alert_wrop {
        padding: 10px;
        display: flex;
        justify-content: space-between;
    }

    .fontcss {
        font-family: "Microsoft YaHei", "微软雅黑", "Helvetica Neue", Helvetica,
        "PingFang SC", "Hiragino Sans GB", Arial, sans-serif;
        color: #333;
        font-weight: bold;
    }
</style>


