/**
* 法律法规组件
* @module 案件
* @desc 查看
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <el-dialog
            :close-on-click-modal="false"
            :title="title"
            :visible="show"
            @close="closeDialog"
            width="750px"
            v-loading="loading"
    >
        <div class="detail-block" v-if="form">
            <div class="isDisable">
                <!-- <div class="enforceCode">编号：000</div> -->
                <div>生效状态：{{ form.isEffective==0?'待生效':(form.isEffective==1)?'已生效':'待生效' }}</div>
            </div>
            <h3>{{form.reasonName}}</h3>
            <div class="enforceCode">
                <span>案由编号：{{form.reasonNum}}</span>
            </div>

            <el-divider></el-divider>

            <div class="enforceRight-block" >
                <el-form :model="form" label-width="150px" ref="ruleForm" size="medium">
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="执法领域：" prop="enforceId">
                                <span class="fontcss">{{form.enforceName}}</span>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="法律法规：" prop="lawId">
                                <span class="fontcss">{{form.lawName}}</span>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="处罚事项：" prop="pmId">
                                <span class="fontcss">{{form.pmName}}</span>
                            </el-form-item>
                        </el-col>
                        <el-col :span="24">
                            <el-form-item label="违反条例：">
                                <span class="fontcss">{{form.violationRegulations}}</span>
                            </el-form-item>
                        </el-col>
                        <el-col :span="24">
                            <el-form-item label="违反条例内容：">
                                <span class="fontcss" v-html="form.violationRegulationsContent"></span>
                            </el-form-item>
                        </el-col>
                        <el-col :span="24">
                            <el-form-item label="依据条例：">
                                <span class="fontcss">{{form.accordingRegulations}}</span>
                            </el-form-item>
                        </el-col>
                        <el-col :span="24">
                            <el-form-item label="依据条例内容：">
                                <span class="fontcss">{{form.accordingRegulationsContent}}</span>
                            </el-form-item>
                        </el-col>
                        <el-col :span="24">
                            <el-form-item label="职权依据：">
                                <span class="fontcss">{{form.rightclause}}</span>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="自然人罚款范围：">
                                <span class="fontcss">{{form.naturalRangeMin}}-{{form.naturalRangeMin}}元</span>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="法人罚款范围：">
                                <span class="fontcss">{{form.legalRangeMin}}-{{form.legalRangeMin}}元</span>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
            </div>
            <el-divider content-position="right" v-if="form.effectiveTime">
        <span class="divider-span">
          生效时间：{{ form.effectiveTime | formatDate}}
          <span
                  v-if="form.invalidTime"
          >至 {{ form.invalidTime | formatDate}}</span>
        </span>
            </el-divider>
        </div>
    </el-dialog>
</template>

<script>
    import {formatDate} from "../../../../until/wordBook";
    import {apiCaseReasonGet} from "../../../../api/case";

    export default {
        name: "seeContent",
        props: {
            editId: {
                type: String,
                default: ""
            },
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: ""
            }
        },
        data() {
            return {
                loading:true,
                form: {},
                enforceIdArr: [], //执法领域
                lawArr: [], //法律法规
                fineArr: [] //罚款
            };
        },
        methods: {
            closeDialog() {
                this.$emit("update:show", false);
            },

            //弹框弹出时获取data给form
            initPage() {
                apiCaseReasonGet(this.editId).then(res => {
                    this.loading=false;
                    this.form = res.data;
                    console.log(this.form)
                });
            }
        },
        filters: {
            //时间戳
            formatDate(val) {
                return formatDate(val, "yyyy-MM-dd");
            }
        },

        //监听，如果show为true,则执行initPage()
        watch: {
            show(value) {
                if (value) {
                    this.initPage();
                }
            }
        }
    };
</script>

<style lang="less" scoped>
    .concss {
        font-weight: bold;
    }

    .enforceCode {
        text-align: center;
        color: #aaaaaa;
        font-size:  @fontSize ;
        margin-top: 5px;
    }

    .fontcss {
        font-family: "Microsoft YaHei", "微软雅黑", "Helvetica Neue", Helvetica,
        "PingFang SC", "Hiragino Sans GB", Arial, sans-serif;
        color: #333;
        font-weight: 500;
        white-space: pre-wrap;
    }

    .detail-block {
        font-size:  @fontSize + 4;

        h3 {
            text-align: center;
        }

        .enforceCode {
            float: right;
        }

        .enforceRight-block {
            padding: 10px 20px 0 20px;

            .enforceRight {
                text-indent: 2em;
                margin: 10px 0;
            }
        }

        .isDisable {
            padding: 0 20px;
            font-size:  @fontSize ;
            color: #888;
        }
    }
</style>
