/**
* 法律法规组件
* @module 案件
* @desc 列表
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <div class="wrop-search">
            <lj-search-form :onAdd="onAdd" :searchObj="searchObj" @searchList="searchList"/>
        </div>
        <!-- 表格 -->
        <el-table
                :data="list"
                :header-cell-style="{background:'#e9eff8'}"
                :row-style="{cursor:'pointer'}"
                @row-click="seeFunction"
                border
                v-loading="loading"
        >
            <el-table-column :show-overflow-tooltip="true" align="center" label="编号" prop="reasonNum"/>

            <el-table-column :show-overflow-tooltip="true" align="center" label="案由名称" prop="reasonName"/>
            <el-table-column
                    :show-overflow-tooltip="true"
                    align="center"
                    label="违反条例"
                    prop="violationRegulations"
            />
            <el-table-column
                    :show-overflow-tooltip="true"
                    align="center"
                    label="违反条例内容"
                    prop="violationRegulationsContent"
            />
            <el-table-column
                    :show-overflow-tooltip="true"
                    align="center"
                    label="依据条例"
                    prop="accordingRegulations"
            />
            <el-table-column
                    :show-overflow-tooltip="true"
                    align="center"
                    label="职权依据"
                    prop="rightclause"
            />
            <el-table-column align="center" label="是否生效" prop="isEffective" width="100">
                <template slot-scope="scope">{{scope.row.isEffective|isEffective}}</template>
            </el-table-column>
            <el-table-column align="center" label="操作" width="180">
                <template slot-scope="scope">
                    <el-button @click.stop="HistoryFunction(scope.row)" plain size="mini" type="primary">历史版本
                    </el-button>
                    <el-button
                            @click.stop="changeFunction(scope.row)"
                            plain
                            size="mini"
                            type="warning"
                            v-if="buttonCheck('edit')"
                    >修订
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <lj-pagination
                :pageNum.sync="form.pageNum"
                :pageSize.sync="form.pageSize"
                :total="total"
                @getData="getList"
                class="page-top"
        />

        <!-- 编辑&添加弹框 -->
        <change-Edit
                :okbtn="okbtn"
                :pmId="pmId"
                :tid="tid"
                :show.sync="editObj.show"
                :title="editObj.title"
                :type="editObj.editId"
                @getList="getList"
        />
        <History :TableObj="TableObj" @getList="getList"></History>
        <!-- 查看 -->
        <see-content :editId="seeObj.editId" :show.sync="seeObj.show" :title="seeObj.title"></see-content>
    </div>
</template>

<script>
    import LjPagination from "../../../../components/LjPagination";
    import History from "./historyTable.vue";
    import LjSearchForm from "../../../../components/LjSearchForm";
    import {isEffective} from "../../../../until/wordBook";
    import changeEdit from "./editadd";
    import seeContent from "./seeContent";
    import {apiCaseReasonSearch} from "../../../../api/case";

    export default {
        name: "causePage",
        props: ["pmId",'lawVersion','tid'],
        data() {
            return {
                loading:true,
                //弹出框数据
                editObj: {
                    show: false,
                    title: "",
                    editId: null
                },
                TableObj: {
                    title: "",
                    editId: null,
                    show: false
                },
                seeObj: {
                    show: false,
                    title: "",
                    editId: null
                },
                okbtn: null,
                //搜索组件数据
                searchObj: null,
                form: {
                    pageSize: 20,
                    pageNum: 1,
                    isEffective: 1
                },
                total: 0,
                list: []
            };
        },
        components: {
            changeEdit,
            LjPagination,
            LjSearchForm,
            History,
            seeContent
        },

        methods: {
            //查看
            seeFunction(row) {
                this.seeObj.show = true;
                this.seeObj.title = "查看案由";
                this.seeObj.editId = row.crId;
            },
            //查看历史版本
            HistoryFunction(row) {
                this.TableObj = {
                    editId: row.reasonId,
                    title: "查看历史版本",
                    show: true
                };
            },
            //修订版本
            changeFunction(row) {
                this.editObj.editId = row.crId;
                this.editObj.title = "修订案由版本";
                this.editObj.show = true;
                this.okbtn = "3";
            },
            //新增
            onAdd() {
                this.editObj.editId = null;
                this.editObj.title = "新增案由";
                this.editObj.show = true;
                this.okbtn = "2";
            },
            //编辑
            editFunction(row) {
                this.editObj.editId = row.crId; //根据是否传editId判断是否是编辑和添加
                this.editObj.title = "修改";
                this.editObj.show = true;
                this.okbtn = "1";
            },

            //删除按钮事件
            // delateFunction(row) {
            //     this.$confirm('此操作将永久删除, 是否继续?', '提示', {
            //         confirmButtonText: '确定',
            //         cancelButtonText: '取消',
            //         type: 'warning'
            //     }).then(() => {
            //         apicCaseReasonDelete(row.crId).then(res => {
            //             this.getList();
            //             this.$message({
            //                 type: 'success',
            //                 message: '删除成功!'
            //             });
            //         })
            //     })
            // },
            //搜索
            searchList(data) {
                //data就是接收的this.form数据
                data.pageSize = this.form.pageSize;
                data.enforceId = this.enforceId;
                data.pageNum = 1;
                data.isEffective = 1;
                data.pmId = this.pmId;
                this.loading = true;
                data.pageSize = this.form.pageSize;
                data.pmVersion = this.lawVersion
                apiCaseReasonSearch(data).then(res => {
                    this.list = res.data;
                    this.total = res.total;
                    this.loading = false;
                });
            },
            //查询+搜索
            getList() {
                this.form.pmId = this.pmId;
                 this.form.pmVersion = this.lawVersion;
                apiCaseReasonSearch(this.form).then(res => {
                    this.total = res.total;
                    this.list = res.data;
                    this.loading = false;

                });
            }
        },
        filters: {
            //时间戳
            formatDate(value) {
                return formatDate(value, "yyyy-MM-dd hh:mm:ss");
            },

            //是否
            isEffective(val) {
                return isEffective[val];
            }
        },
        watch: {
            pmId() {
                this.getList();
            }
        },
        created() {
            this.getList();
        }
    };
</script>

<style scoped>
    .wrop-search {
        margin-bottom: 10px;
    }

    .page-top {
        margin-top: 10px;
    }
</style>
